<template>
	<div id="mapid">
		<l-map :zoom="zoom" ref="map">
			<l-tile-layer :url="url"></l-tile-layer>
			<MapJsonComponent
					v-for="(item, index) in filterItems"
					:key="index"
					:parametrs="item"
				/>
		</l-map>
		<div class="toggle-wrapper">
			<div class="input-group">
				<select class="custom-select" v-model="activeItem">
					<option
						v-for="(item, index) in activeMap"
						:value="item.val"
						:key="index"
					>
						{{item.title}}
					</option>
				</select>
			</div>
		</div>
	</div>
</template>

<script>
import { LMap, LTileLayer, LMarker, LPopup } from "vue2-leaflet";
import MapJsonComponent from '@/components/MapWrapper/MapComponents/MapJsonComponent.vue'
import { mapGetters, mapState, mapMutations, mapActions } from 'vuex'
export default {
	name: "MapWrapper",
	components: {
		LMap,
		LTileLayer,
		MapJsonComponent
	},
	data: () => ({
		activeMap: [
			{title: 'getOrbisSystemJson', val: 'getOrbisSystemJson'},
			{title: 'portalCsv', val: 'portalCsv'},
			{title: 'barsGeoJson', val: 'barsGeoJson'},
		],
		activeItem: 'portalCsv',
		activeParametrs: [],
		zoom: 6,
		center: [],
		url: "http://{s}.tile.osm.org/{z}/{x}/{y}.png",
	}),
	computed: {
		...mapState([
			'filterActiveArr',
			'centerItem'
		]),
		...mapGetters([
			'filterItems'
		])
	},
	watch: {
		async activeItem(val) {
			localStorage.setItem('activeMapItem', val)
			this.$store.dispatch('changeActiveFilter', val)
			this.$store.dispatch('findCenter', 0) // Поиск по первому элементу массива

			this.$refs['map'].setCenter(this.centerItem)
		},
		centerItem() {
			this.$refs['map'].setZoom(10)
			this.$refs['map'].setCenter(this.centerItem)
		}
	},
	async created() {
		await this.$store.dispatch('getAllMaps')
		this.$store.dispatch('findCenter', 0)
		const ls = localStorage
		const activeMapItem = ls.getItem('activeMapItem')

		if (activeMapItem) {
			this.activeItem = activeMapItem
			this.$store.dispatch('changeActiveFilter', activeMapItem)
			// this.$refs['map'].setCenter(this.centerItem)
		} else {
			ls.setItem('activeMapItem', this.activeItem)
			this.$store.dispatch('changeActiveFilter', this.activeItem)
		}
	},

};
</script>

<style lang="scss">
#mapid {
	height: 50vh;
	position: relative;
}

.toggle-wrapper {
	position: fixed;
	top: 20px;
	right: 20px;
	height: 20px;
	width: 200px;
	// background-color: rgba(0,0,0,1);
	z-index: 999;
}
</style>
