import axios from 'axios'
import Papa from 'papaparse'

export default class Api {
	static async getOrbisSystemJson() {
		const {data} =  await axios('~/resourses/orbis-system.json')
		return data.features
	}

	static async portalCsv() {
		const {data} = await axios.get('~/resourses/portals.csv')
		const parseData = await Papa.parse(data, {
			complete(results) {
				return results
			}
		})
		const filterResult = parseData.data.map(item => {
			return {
				id: item[0],
				geometry: {
					coordinates: [item[6], item[7]]
				},
				properties: {
					name: item[2],
					name_en: item[3],
				}
			}
		})
		return filterResult
	}

	static async barsGeoJson() {
		const {data} =  await axios('~/resourses/bars.geojson')
		return data.features
	}

	static async getAllObjects() {
		const getOrbisSystemJson = await this.getOrbisSystemJson()
		const portalCsv = await this.portalCsv()
		const barsGeoJson = await this.barsGeoJson()

		return {
			getOrbisSystemJson,
			portalCsv,
			barsGeoJson
		}
	}
}