import Vue from "vue"
import Vuex from "vuex"
import Api from '../helpers/api'


Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		getOrbisSystemJson: [],
		portalCsv: [],
		barsGeoJson: [],
		filterActiveArr: [],
		activeField: 'getOrbisSystemJson',
		centerItem: [],
		query: ''
	},
	getters: {
		filterItems: state => state[state.activeField].filter(item => {
			return item.properties.name.toLowerCase().indexOf(state.query.toLowerCase()) > -1
		})
	},
	mutations: {
		mutateAllMapsObj(state, payload) {
			state.getOrbisSystemJson = payload.getOrbisSystemJson
			state.portalCsv = payload.portalCsv
			state.barsGeoJson = payload.barsGeoJson
			state.filterActiveArr = payload.getOrbisSystemJson
		},
		mutateFilterActiveArr(state, payload) {
			state.query = payload
		},
		changeFilterActiveArr(state, payload) { // Изменение активного поля
			state.activeField = payload
			state.filterActiveArr = state[payload]
		},
		changedCenterItem(state, payload) { // Поиск центра
			const center = state.filterActiveArr[payload].geometry.coordinates
			state.centerItem = center
		}

	},
	actions: {
		async getAllMaps({commit}) {
			const data = await Api.getAllObjects()
			commit('mutateAllMapsObj', data)
		},
		searchAction({commit}, payload) {
			commit('mutateFilterActiveArr', payload)
		},
		changeActiveFilter({commit}, payload) { // Изменение активного поля
			// console.log(`payload`, payload)
			commit('changeFilterActiveArr', payload)
		},
		findCenter({commit}, payload) { // Поиск центра
			commit('changedCenterItem', payload)
		}
	},
})
