<template>
	<div class="table-wrapper">
			<h1>Таблица</h1>
		<div class="input-group input-group-sm">
			<input type="text" class="form-control" v-model="search">
		</div>
		<table class="table">
			<thead>
				<tr>
					<th scope="col">Название</th>
					<th scope="col">Название</th>
					<th scope="col">Вид деятельности</th>
					<th scope="col">Мунипуциальное образование</th>
					<th scope="col">Город</th>
					<th scope="col">Адрес</th>
					<th scope="col">Адрес</th>
					<th scope="col">Факс</th>
					<th scope="col">Электронная почта</th>
					<th scope="col">Сайт</th>
					<th scope="col">Краткая информация</th>
				</tr>
			</thead>
			<tbody>
				<tr
					v-for="(item, index) in filterItems"
					:key="index"
					@click="findTr(index)"
				>
					<td scope="row" v-if="item.properties.name">{{ item.properties.name }} </td>
					<td v-if="item.properties.name_en">{{ item.properties.name_en }} </td>
					<td v-if="item.properties.vid_dejatelnosti">{{ item.properties.vid_dejatelnosti }}</td>
					<td v-if="item.properties.municipalnoe_obrazovanie">{{ item.properties.municipalnoe_obrazovanie }}</td>
					<td v-if="item.properties.gorod">{{ item.properties.gorod }}</td>
					<td v-if="item.properties.adres">{{ item.properties.adres }}</td>
					<td v-if="item.properties.address">{{ item.properties.address }}</td>
					<td v-if="item.properties.telefonfaks">{{ item.properties.telefonfaks }}</td>
					<td v-if="item.properties.jelektronnaja_pochta">{{ item.properties.jelektronnaja_pochta }}</td>
					<td v-if="item.properties.veb_sajt">{{ item.properties.veb_sajt }}</td>
					<td v-if="item.properties.kratkaja_informacija_ob_organizacii">{{ item.properties.kratkaja_informacija_ob_organizacii }}</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
import { mapGetters, mapState, mapMutations, mapActions } from 'vuex'
export default {
	name: 'TableComponent',
	data: () => ({
		search: '',
	}),
	watch: {
		search(newVal, oldVal) {
			this.$store.dispatch('searchAction', newVal)
		}
	},
	computed: {
		...mapGetters([
			'filterItems'
		])
	},
	methods: {
		findTr(val) {
			setTimeout(() => {
				this.$store.dispatch('findCenter', val)
			}, 250);
		}
	},
};
</script>

<style lang="scss">
	.table-wrapper {
		height: 50vh;
		width: 100%;
		// overflow: hidden;
		overflow: auto;
		& .table {
			height: 100%;
			width: 100%;
			& tr {
				cursor: pointer;
			}
		}
	}
</style>